.popup__save-button {
  width: 358px;
  height: 50px;
  color: #fff;
  background-color: #000;
  border-radius: 2px;
  margin-bottom: 37px;
  opacity: 1;
}

.popup__save-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 450px) {
  .popup__save-button {
    width: 238px;
    height: 46px;
    margin-top: 25px;
  }
}